import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { API_CONFIG } from 'src/config/api.config';
import { CreateDespesaDTO } from '../../interfaces/despesas/CreateDespesaDTO';
import { DespesaDTO } from '../../interfaces/despesas/DespesaDTO';

@Injectable({
  providedIn: 'root'
})
export class DespesaService {

  constructor(
    private http: HttpClient,
  ) { }



  list(): Observable<DespesaDTO[]> {
    return this.http.get<DespesaDTO[]>(`${API_CONFIG.baseURL}/despesa`, {
      responseType: 'json',
    });
  }

  create(newDespesa: CreateDespesaDTO): Observable<any> {
    return this.http.post<any>(
      `${API_CONFIG.baseURL}/despesa`,
      newDespesa,
      {
        responseType: 'json',
      });
  }


  update(updatedDespesa: CreateDespesaDTO, despesa_id: string): Observable<DespesaDTO> {
    return this.http.put<DespesaDTO>(
      `${API_CONFIG.baseURL}/despesa/${despesa_id}`,
      updatedDespesa,
      {
        responseType: 'json',
      });
  }

}
