import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { API_CONFIG } from 'src/config/api.config';
import { CreateProdutoDTO, ProdutoDTO, UpdateProdutoDTO } from '../../interfaces/produtos/ProdutoDTO';

@Injectable({
  providedIn: 'root'
})
export class ProdutoService {

  constructor(
    private http: HttpClient,
  ) { }

  list(): Observable<ProdutoDTO[]> {
    return this.http.get<ProdutoDTO[]>(`${API_CONFIG.baseURL}/produto`, {
      responseType: 'json',
    });
  }

  create(newProduto: CreateProdutoDTO): Observable<any> {
    return this.http.post<any>(
      `${API_CONFIG.baseURL}/produto`,
      newProduto,
      {
        responseType: 'json',
      });
  }


  update(updatedProduto: UpdateProdutoDTO, produto_id: string): Observable<ProdutoDTO> {
    console.log(updatedProduto)
    return this.http.put<ProdutoDTO>(
      `${API_CONFIG.baseURL}/produto/${produto_id}`,
      updatedProduto,
      {
        responseType: 'json',
      });
  }

}
